import React from 'react';
import './App.css';
import Nav from "./components/NavBar/Nav";
import Portfolio from "./components/Portfolio/Portfolio";
import Blog from "./components/Blog/Blog";
import ArticleOne from './components/Blog/Articles/ArticleOne';
import ArticleTwo from './components/Blog/Articles/ArticleTwo';
import ArticleThree from './components/Blog/Articles/ArticleThree';
import ArticleFour from './components/Blog/Articles/ArticleFour';
import { BrowserRouter, Route, Switch } from "react-router-dom";


function App() {

  return (
    <div className="App">
      <header className="App-header">
        <BrowserRouter>
          <Nav />
          <Switch>
            <Route path="/portfolio" component={Portfolio} />
            <Route exact path="/blog" component={Blog} />
            <Route path="/blog/ArticleOne" component={ArticleOne} />
            <Route path="/blog/ArticleTwo" component={ArticleTwo} />
            {/* <Route path="/blog/ArticleThree" component={ArticleThree} />  */}
            {/* <Route path="/blog/ArticleFour" component={ArticleFour} /> */}
          </Switch>
        </BrowserRouter>
      </header>
    </div>
  );
}

export default App;
