import React from "react";
import Latex from "react-latex";
import articleCoverImg from "../../../images/article/A2_img/cover.jpeg";
import figureOne from "../../../images/article/A2_img/figure1.png";
import figureTwo from "../../../images/article/A2_img/figure2.png";
import figureThree from "../../../images/article/A2_img/figure3.png";
import figureFour from "../../../images/article/A2_img/figure4.png";
import figureFive from "../../../images/article/A2_img/figure5.png";
import figureSix from "../../../images/article/A2_img/figure6.png";
import figureSeven from "../../../images/article/A2_img/figure7.png";
import figureEight from "../../../images/article/A2_img/figure8.png";
import tableOne from "../../../images/article/A2_img/table1.png";
import tableTwo from "../../../images/article/A2_img/table2.png";
import tableThree from "../../../images/article/A2_img/table3.png";
import tableFour from "../../../images/article/A2_img/table4.png";
import tableFive from "../../../images/article/A2_img/table5.png";
import model from "../../../images/article/A2_img/model.png";
import jacobian from "../../../images/article/A2_img/jacobian.png";
import "./Articles.css";

const ArticleTwo = () => {
    return (
        <div className="article">
            <div className="title">
                Relationship between Mosquito Population Change and Senegal's 2001 Yellow Fever Epiidemic
            </div>
            <p style={{textAlign: "left", marginTop: "-5px", paddingLeft: "20px", fontFamily: "'Quattrocento', serif"}}>Authors: Beth Ding, Tiansheng Zhang, Brianna Rogers-Kumar</p>
            <p style={{textAlign: "left", marginTop: "-5px", paddingLeft: "20px", fontFamily: "'Quattrocento', serif"}}>March. 16 2021</p>
            <img src={articleCoverImg} alt="" style={{padding: "20px"}}/>
            <div className="body">
                <div style={{fontWeight: "600"}}>Introduction</div>
                Yellow Fever is a short duration viral disease spread by the bite of infected female mosquitoes. 
                As ectotherms, their development and survival rates depend on climate [1]. 
                Research from the 1900s proved that yellow fever is only transmitted to humans by mosquitoes, 
                from this we can infer that mosquito population impact on yellow fever transmission is larger than any other 
                vector [2]. Ninety percent of the yellow fever cases occur in global tropical and subtropical areas and the 
                infected population continues to rise every year due, in part, to changing climate [3]. In 2002, the World 
                Health Organization reported continuously on yellow fever cases occurring in Senegal, South Africa which 
                resides within the region of tropics and subtropics and thus experiences a warm tropical climate throughout 
                the year. Starting from January, eighteen cases were reported in Senegal's rural regions Diourbel and Ziguinshor. 
                Then from October to November, the region of infection expanded to urban areas like Touba and Dakarthe and 
                infections spiked from twelve cases to sixty cases. 
                Figure 1 depicts yellow fever cases throughout 2002 in Senegal.  

                <div style={{width: "100%"}}><img src={figureOne} alt="" style={{width: "60%", objectFit: "contain", marginTop: "30px"}}/></div>
                <div style={{marginTop: "-20px"}}>FIGURE 1: Yellow Fever infected case graph in 2002.</div>

                <div style={{marginTop: "20px", lineHeight: "25px"}}>TABLE 1: Yellow Fever infection data in Senegal of 2002.</div>
                <div style={{width: "100%"}}><img src={tableOne} alt="" style={{width: "35%", objectFit: "contain", marginBottom: "30px"}}/></div>

                Yellow fever transmission is highly affected by mosquitoes' habitat which is dependent on the temperature [1]. 
                Therefore, lower rates of infection are expected in climates less favourable to mosquito survival. 
                In part, this helps to explain how Senegals high humidity and year long tropical temperatures contributed 
                to their sudden increase in yellow fever. 
                The mathematical model and following analysis done in the article will propose an explanation of the impact 
                of climate on yellow fever transmission during 2002 in Senegal by focusing on what happens to the population 
                of infected individuals if the mosquito population is high in tropical climates and what happens to the 
                population of infected individuals if the mosquito population is significantly reduced due to colder and 
                drier climate.

                <div style={{fontWeight: "600", marginTop: "30px"}}>Formulation of the mathematical model</div>
                This article discusses the formulation and analysis of an SIR approach to modelling Senegals 2002 yellow 
                fever epidemic, which accounts for the existence of vaccines and varying mosquito populations. 
                The supporting differential equations will take humans, mosquitoes, and monkeys into consideration. 

                <div style={{width: "100%"}}><img src={figureTwo} alt="" style={{width: "40%", objectFit: "contain", marginTop: "30px"}}/></div>
                <div style={{marginTop: "-20px"}}>FIGURE 2: Float diagram of the yellow fever model.</div>

                <div style={{marginTop: "20px", lineHeight: "25px"}}>TABLE 2: List of variables.</div>
                <div style={{width: "100%"}}><img src={tableTwo} alt="" style={{width: "55%", objectFit: "contain"}}/></div>

                <div style={{marginTop: "20px", lineHeight: "25px"}}>TABLE 3: List of parameters.</div>
                <div style={{width: "100%"}}><img src={tableThree} alt="" style={{width: "100%", objectFit: "contain", marginBottom: "30px"}}/></div>

                Causation for the 2002 spike in cases is unknown and documentation of the populations of mosquitoes and 
                monkeys is limited during this time period. Though Senegals population is reported during this time, 
                this articles analysis only considers the year of 2002, birth and death in the human population will be 
                insignificant and hence ignored in our model. Additionally, it is assumed that all infected humans can 
                recover from the disease and all recovered and vaccinated individuals will be immune to the disease 2002 
                strain. With the available variables, parameters and assumptions, equations were developed as follows:
                <div style={{width: "100%"}}><img src={model} alt="" style={{width: "80%", objectFit: "contain", margin: "30px 0px"}}/></div>

                The rate of new incidents of mosquito bites from infected mosquitoes to susceptible individuals is given 
                by <Latex>$\beta_m S_i I_m$</Latex>. These bites will assume to result in infection and thus will be subtracted from the rate of change 
                in susceptible individuals over time <Latex>$S_i'$</Latex>, and added to the rate of change in infected individuals over 
                time <Latex>$I_i'$</Latex>. The rate of susceptible individuals getting vaccinated is denoted by <Latex>$vS_i$</Latex>. We assume that vaccination 
                results in immunity and this population is removed from the susceptible population, similarly the recovered 
                population <Latex>$\alpha I_i$</Latex> are removed from the infected population because the model assumes they are unable to transmit 
                or obtain the disease. The rate at which susceptible mosquitoes bite infected monkey is given by <Latex>$rMS_m$</Latex> and bites 
                from susceptible mosquitoes to infected individual is given by <Latex>$\beta_i S_m I_i$</Latex>. Susceptible mosquitoes, after biting the 
                infected monkeys or individuals, will be infected with Yellow Fever, thus subtracted from the rate of change in 
                susceptible mosquitoes over time <Latex>$S_m'$</Latex>, and added to the rate of change in infected mosquitoes over time <Latex>$I_m'$</Latex>. 
                The birth rate of susceptible and infected mosquitoes is denoted by <Latex>$b(S_m+I_m)$</Latex> which is added to the rate of change 
                in susceptible mosquitoes over time as new births increase the mosquito population. The normal death rate of 
                susceptible mosquitoes is denoted by <Latex>$dS_m$</Latex> which is subtracted from the rate of change in susceptible mosquitoes 
                over time as death decreases the mosquito population. Death rate, whether by natural causes or by yellow fever 
                is denoted by <Latex>$I_m(d+o)$</Latex> which is subtracted from the rate of change in infected mosquitoes over time.

                <div style={{fontWeight: "600", marginTop: "30px"}}>Solution of the problem</div>

                We will be using sensitivity analysis, an SIR graph and the Jacobian matrix method with the optimal parameters 
                collected with the data fitting method to solve our mathematical model. Operator ode23 wll be used for the 
                method discussed above along with MATLAB as well. To obtain our optimal parameters, 
                we set our guessed parameters and initial variables to the possible values listed below in table 4:

                <div style={{marginTop: "20px", lineHeight: "25px"}}>TABLE 4: Guessed parameters and initial variables.</div>
                <div style={{width: "100%", marginBottom: "30px"}}><img src={tableFour} alt="" style={{width: "70%", objectFit: "contain"}}/></div>

                We then use ode23 and our data fitting code to perform the method and we obtain the optimal parameters and 
                values along with the best fitting graph comparing with the official data from the WHO site shown below 
                in table 5 and figure 3:

                <div style={{marginTop: "20px", lineHeight: "25px"}}>TABLE 5: Collected data for the optimal parameters.</div>
                <div style={{width: "100%"}}><img src={tableFive} alt="" style={{width: "40%", objectFit: "contain"}}/></div>

                <div style={{width: "100%"}}><img src={figureThree} alt="" style={{width: "70%", objectFit: "contain", marginTop: "30px"}}/></div>
                <div style={{marginTop: "-20px", marginBottom: "30px"}}>FIGURE 3: Data fitting graph.</div>

                With our optimal parameters and initial variables, we can solve our model with sensitivity analysis with MATLAB. 
                We first perform sensitivity analysis for the importance of the transmission coefficient from the infected 
                mosquitoes to the susceptible individuals (<Latex>$\beta_i$</Latex>) and the recovery rate (<Latex>$\alpha$</Latex>) on the population of infected 
                individuals (<Latex>$S_i$</Latex>), and we obtain our graph shown below in figure 4: 

                <div style={{width: "100%"}}><img src={figureFour} alt="" style={{width: "70%", objectFit: "contain", marginTop: "30px"}}/></div>
                <div style={{marginTop: "-20px", marginBottom: "30px"}}>FIGURE 4: Graph of sensitivity analysis for <Latex>$I_i$</Latex>.</div>

                Then we apply the same method for the importance of the transmission coefficient from the transmission 
                coefficient from infected individual to susceptible mosquitoes (<Latex>$\beta_i$</Latex>), the transmission coefficient from 
                infected monkeys to susceptible mosquitoes (<Latex>$r$</Latex>), the death rate of mosquitoes (<Latex>$d$</Latex>), and the death rate of 
                mosquitoes caused by yellow fever (<Latex>$o$</Latex>) on the infected mosquito population (<Latex>$I_m$</Latex>), and we obtain the graph 
                shown below in figure 5:

                <div style={{width: "100%"}}><img src={figureFive} alt="" style={{width: "70%", objectFit: "contain", marginTop: "30px"}}/></div>
                <div style={{marginTop: "-20px", marginBottom: "30px"}}>FIGURE 5: Graph of sensitivity analysis for <Latex>$I_m$</Latex>.</div>

                We then compute the SIR graph for our model in MATLAB using ode23 with the obtained optimal parameters above. 
                We first perform the method with the normal mosquito population and get the graph shown below in figure 6:

                <div style={{width: "100%"}}><img src={figureSix} alt="" style={{width: "70%", objectFit: "contain", marginTop: "30px"}}/></div>
                <div style={{marginTop: "-20px", marginBottom: "30px"}}>FIGURE 6: SIR graph with normal mosquito population.</div>

                Then, we reduce the initial susceptible mosquito population by 50%, and we obtain the graph shown below in figure 7:

                <div style={{width: "100%"}}><img src={figureSeven} alt="" style={{width: "70%", objectFit: "contain", marginTop: "30px"}}/></div>
                <div style={{marginTop: "-20px", marginBottom: "30px"}}>FIGURE 7: SIR graph with  of normal mosquito population.</div>

                In order to have a better understanding and perform an easier comparison for the infected mosquito population, 
                we plot the graphs again with plotyy, and the comparison graph is shown below in figure 8 with the normal 
                population as <Latex>$Ii0$</Latex>, reduced population as <Latex>$Ii1$</Latex>:

                <div style={{width: "100%"}}><img src={figureEight} alt="" style={{width: "70%", objectFit: "contain", marginTop: "30px"}}/></div>
                <div style={{marginTop: "-20px", marginBottom: "30px"}}>FIGURE 8: Comparison SIR graph after changing axis scale.</div>

                For the jacobian matrix, we calculate it by putting the model into matrix with our optimal parameters in 
                python with module sympy and get:
                
                <div style={{width: "100%"}}><img src={jacobian} alt="" style={{width: "100%", objectFit: "contain", margin: "30px 0px"}}/></div>

                This is a 5*5 matrix therefore the <Latex>$Tr(J)$</Latex> is of no use. <br></br>
                We calculate the eigenvalue with <Latex>$(0, 0, 0, 0, 0)$</Latex> and obtain: <br></br>
                <div style={{textAlign: "center", margin: "0px"}}><Latex>$\gamma = 0, 1.01, 378.66$</Latex></div>
                Two of the solutions are greater than 1, therefore the point is unstable.

                <div style={{fontWeight: "600", marginTop: "30px"}}>Interpretation of results</div>
                The lower temperature threshold for mosquito populations which would allow for transference of diseases 
                is 16 C [1]. Due to the small size of these populations, analysis of populations surviving at or below this 
                temperature is trivial.  Similarly, most species of mosquitoes cannot survive in temperatures exceeding 40 C 
                [1]. Therefore, in considering the effect of climate on the human infected individual population <Latex>$I_i$</Latex> as 
                influenced by mosquito populations, we consider fluctuations in temperature from 16 to 40 C. As suggested 
                by research into the spread of malaria, temperatures around 26 to 29 C present optimal conditions for 
                transmission [5]. As previously discussed, yellow fever presents predominantly  in a region of high risk, 
                which is in direct alignment with the area of tropics and subtropics. Due to what our analysis suggests as 
                the dominating factors impacting infected populations, that is, their transmission rates, which have a strong 
                positive correlation as shown in figures 4 and 5, our model supports that high mosquito populations, such as 
                those seen in tropical climates at or around temperatures of 29 C, will result in the highest transmission 
                rates for yellow fever. Therefore, if the mosquito population is high in areas of tropical climate, the 
                infected individual population <Latex>$I_i$</Latex> will see a maximal rate of increase. <br></br>
                Previously, sensitivity analysis introduced transmission coefficients <Latex>$\beta_m$</Latex> and <Latex>$r$</Latex> as the parameters most 
                important to changes in the infected individual and infected mosquito populations <Latex>$I_i$</Latex> and <Latex>$I_m$</Latex>. Additionally, 
                by altering our initial susceptible mosquito population <Latex>$S_m$</Latex> to observe the effect of this change on <Latex>$I_i$</Latex>, we 
                deduced that a decrease in mosquito population would cause a decrease in infection rate. Thus, if we assume 
                consistency in Senegal's rural monkey populations, our model predicts that a drop in temperature which results 
                in a decrease to population size will impact the infected mosquito population to a greater extent than any 
                other factor. This is shown by the relationship between monkey infection and mosquito infection rates which 
                have a strong positive correlation in our sensitivity analysis. From this analysis we infer that a decrease 
                in mosquito population will result in a decrease in transmission rates from monkey to mosquito and thus a 
                decrease in transmission to humans. Thus, significant reduction in temperature reduces the infected individual 
                population.

                <div style={{fontWeight: "600", marginTop: "30px"}}>Critique of the model</div>
                The equilibrium analysis we are able to perform does not determine non trivial cases. The stability 
                point <Latex>$(0, 0, 0, 0, 0)$</Latex> that we evaluated to be unstable allows us to draw the conclusion that the value of our 
                variables will always tend away from zero, but it does not indicate what non trivial cases there could be 
                with the infected individual population <Latex>$I_i$</Latex> at zero. This analysis, while too complex to be evaluated in 
                this article, could prove vital in determining how future cases  or outbreaks of yellow fever could impact 
                the populations where they occur. Additionally,  research on mosquito population and climate impact on said 
                populations informed our solutions and therefore our conclusion; however,  our model does not directly account 
                for weather dynamics, or resulting breeding/life cycle changes to the mosquito population.  Inclusion of 
                information about mosquito birth, death would require far more data than is available on mosquito populations 
                at the time of the 2002 outbreak in Senegal however, more complex analysis could be done on neighbouring 
                populations and extrapolated to ours in order to offer information about how weather patterns, such as 
                differences between temperature and humidity, or available water sources, directly affect infection rates. 
                Further questions could be asked about the interplay between temperature and moisture and their effect on 
                mosquito population. Finding more data would greatly improve our ability to improve our model. We developed a 
                model which could accurately represent the limited data available to us and therefore does not include 
                parametrization of mosquito populations based on aquatic eggs contrasted to non aquatic eggs, breeding 
                patterns, speciation or temperature. As previously stated, this limits the information able to be taken 
                from our model.  Our model could also be improved by including more information about mosquito-monkey 
                interactions and the monkey population in general. Further information on infection rates in monkeys, their 
                migration patterns and diet would allow for more in depth analysis on the rate of transmission from monkey to 
                mosquito, though our model does not make these inclusions. 

                <div style={{fontWeight: "600", marginTop: "30px"}}>References</div>
                <span style={{fontSize: "13px", lineHeight: "30px", color: "darkblue", verticalAlign: "top"}}>[1]</span>Beck-Johnson, Lindsay M., et al. The Effect of Temperature on Anopheles Mosquito Population Dynamics and the Potential for Malaria Transmission. PLOS ONE, Public Library of Science, 14 Nov. 2013, journals.plos.org/plosone/article?id=10.1371%2Fjournal.pone.0079276. <br></br>
                <span style={{fontSize: "13px", lineHeight: "30px", color: "darkblue", verticalAlign: "top"}}>[2]</span>Driver C. Yellow Fever. Practice Nursing, vol. 21, no. 4, Apr. 2010, pp. 201205. EBSCOhost, doi:10.12968/pnur.2010.21.4.47393.<br></br>
                <span style={{fontSize: "13px", lineHeight: "30px", color: "darkblue", verticalAlign: "top"}}>[3]</span>Martorano Raimundo, Silvia et al. Equilibrium analysis of a yellow Fever dynamical model with vaccination. Computational and mathematical methods in medicine vol. 2015 (2015): 482091. doi:10.1155/2015/482091<br></br>
                <span style={{fontSize: "13px", lineHeight: "30px", color: "darkblue", verticalAlign: "top"}}>[4]</span>Shapiro, Lillian L M et al. Quantifying the effects of temperature on mosquito and parasite traits that determine the transmission potential of human malaria. PLoS biology vol. 15,10 e2003489. 16 Oct. 2017, doi:10.1371/journal.pbio.2003489<br></br>
                <span style={{fontSize: "13px", lineHeight: "30px", color: "darkblue", verticalAlign: "top"}}>[5]</span>Wilke, Andr Barretto Bruno et al. Mosquito populations dynamics associated with climate variations. Acta tropica vol. 166 (2017): 343-350. doi:10.1016/j.actatropica.2016.10.025<br></br>
            </div>
        </div>
    )
}

export default ArticleTwo;